.button-57 {
  position: fixed; /* Fix the button to the bottom-left corner */
  bottom: 80px; /* Distance from the bottom */
  right: 20px; /* Distance from the right*/
  opacity: 0; /* Button starts invisible */
  overflow: hidden;
  border: 2px solid #18181a;
  color: #18181a;
  font-size: 17px;
  font-family: 'Goblin One', 'Notable';
  font-weight: 900;
  line-height: 15px;
  padding: 18px 18px 17px;
  text-decoration: none;
  cursor: pointer;
  background: #fff;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  z-index: 1000; /* Ensure it stays above other elements */
  border-radius: 5px; /* Optional: Rounded corners */
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 1);
}

.button-57.visible {
  opacity: 1; /* Button becomes visible */
}

.button-57 span:first-child {
  position: relative;
  transition: color 600ms cubic-bezier(0.48, 0, 0.12, 1);
  z-index: 10;
}

.button-57 span:last-child {
  color: white;
  display: block;
  position: absolute;
  bottom: 0;
  transition: all 500ms cubic-bezier(0.48, 0, 0.12, 1);
  z-index: 100;
  opacity: 0;
  top: 50%;
  left: 50%;
  transform: translateY(225%) translateX(-50%);
  height: 14px;
  line-height: 13px;
}

.button-57:after {
  content: "";
  color: #ffffff;
  position: absolute;
  bottom: -50%;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(98, 116, 69);
  transform-origin: bottom center;
  transition: transform 600ms cubic-bezier(0.48, 0, 0.12, 1);
  transform: skewY(9.3deg) scaleY(0);
  z-index: 50;
}

.button-57:hover:after {
  transform-origin: bottom center;
  transform: skewY(9.3deg) scaleY(2);
}

.button-57:hover span:last-child {
  transform: translateX(-50%) translateY(-100%);
  opacity: 1;
  transition: all 900ms cubic-bezier(0.48, 0, 0.12, 1);
}

